
import { defineComponent } from "vue";

export default defineComponent({
    name: "NavigationTabsEntry",
    emits: ['update:modelValue'],
    props: {
        modelValue: String,
        tabSlug: {
            type: String,
            required: true
        }
    },
    computed: {
        isActive(): boolean {
            return this.modelValue === this.tabSlug;
        }
    }
})
